.description {
    padding: 20px;
    background-color: #fff;
    text-align: center;
  }
  .avatar {
    width: 140px;
    height: 140px;
    border-radius: 50%;
  }
  .name {
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    color: brown;
  }
  .tag {
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: brown;
  }
  .location {
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    color: brown;
  }
  .profile {
    width: 400px;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    background-color: #fff;
  }
  
  .stats {
    display: flex;
    flex-wrap: nowrap;
  
    justify-content: center;
    align-items: normal;
    margin-bottom: 0;
    padding-bottom: 0;
  }
  
  .item {
    display: flex;
    flex-direction: column;
  
    width: calc((100%) / 3);
    height: 60px;
    margin-right: 0;
    margin-bottom: 0;
    border: 1px solid #e7ecf2;
    background-color: #f3f6f9;
  }
  .label {
    text-transform: capitalize;
    text-align: center;
    padding-top: 8px;
    font-size: 16px;
    font-weight: 400;
    color: #82909e;
  }
  .quantity {
    text-align: center;
    padding-bottom: 20px;
    font-size: 18px;
    font-weight: 500;
    color:  darkorange
  }